import React, {useState} from 'react';
import './styles.css';

function Skills() {
  const [showText, setShowText] = useState(false);
  const [currentText, setCurrentText] = useState(' ');

  const toggleText = (text) => {
    setShowText(text !== currentText || !showText);
    setCurrentText(text);
  };

  const textMappings = {
    'Python': 'O Python  uma linguagem de programao de alto nvel, interpretada e de propsito geral. Foi criada por Guido van Rossum e lanada pela primeira vez em 1991. ...',
    'React': 'React JS  uma biblioteca JavaScript para a criao de interfaces de usurio  ou UI (user interface). Criado em 2011 pelo time do Facebook.',
    'Figma': 'Figma  um editor grfico de vetor e prototipagem de projetos de design baseado principalmente no navegador web.',
    'Git': 'O Git  um sistema de controle de verso distribudo gratuito e de cdigo aberto projetado para lidar com tudo, desde pequenos at Projetos muito grandes com rapidez e eficincia.',
    'Html': 'HTML  uma linguagem de marcao utilizada na construo de pginas na Web. Documentos HTML podem ser interpretados por navegadores.',
    'Css': 'CSS  chamado de linguagem Cascading Style Sheet e  usado para estilizar elementos escritos em uma linguagem de marcao como HTML.',
    'Javascript': 'JavaScript  uma linguagem de programao que adiciona interatividade ao seu site. Isso acontece em jogos, no comportamento das respostas quando botes so pressionados ou com entrada de dados em formulrios; com estilo dinmico; com animao, etc.',
  };

  return(
    <>
    <div className='skills' id='skills'>
    <h2 className='title-page'>Minhas Habilidades</h2>
    <div className='container-skills'>

      <div class="icons">
        <div class="icon-container">
          <i class="devicon-python-plain-wordmark colored single-item clickable"
          onClick={() => toggleText('Python')}

          ></i>
        </div>
        <div class="icon-container">
          <i class="devicon-react-original colored single-item clickable"
          onClick={() => toggleText('React')}

          ></i>
        </div>
        <div className='icon-container'>
          <i class="devicon-figma-plain colored single-item clickable"
          onClick={() => toggleText('Figma')}

          ></i>
        </div>
        <div className='icon-container'>
          <i class="devicon-git-plain colored single-item clickable"
          onClick={() => toggleText('Git')}

          ></i>
        </div>
        <div class="icon-container">
          <i class="devicon-html5-plain-wordmark colored single-item clickable"
          onClick={() => toggleText('Html')}

          ></i>
        </div>
        <div class="icon-container">
          <i class="devicon-css3-plain-wordmark colored single-item clickable"
          onClick={() => toggleText('Css')}

          ></i>
        </div>
        <div class="icon-container">
          <i class="devicon-javascript-plain colored single-item clickable"
          onClick={() => toggleText('Javascript')}

          ></i>
        </div>
    </div>
    <div className='text-container'>
        <h4>{currentText}</h4>
        {showText && (
            <h5>{textMappings[currentText]}</h5>
          )}


    </div>
   </div>
   </div>
   </>
  );
}

export default Skills;
